<template>
  <div class="register">
    <Header title=""></Header>
    <div v-mx v-mt>
      <Field v-model="phone" placeholder="" />
      <Field v-model="password" type="password" showPassword placeholder="" />
      <Field v-mt v-model="vailCode" type="password" placeholder="">
        <div slot="addon" v-ml>
          <Button v-px="12" v-py="12"
          is-send-code
          :sendPhoneNo="phone"
          type="primary" round/>
        </div>
      </Field>
      <Button v-my @click="submit" class="register-btn" type="primary" block round></Button>
      <Button type="primary"  outline to="/login" block round></Button>
      <p v-mt>
        <input v-model="argument" type="checkbox"> 
        <router-link to="/pro"></router-link>
      </p>
    </div>
  </div>
</template>
<script>
import Header from "../components/Header.vue";
import Field from "../components/Field.vue";
import Button from "../components/Button.vue";
import { isEmpty } from "lodash";
export default {
  data() {
    return {
      phone: "",
      password: "",
      vailCode: "",
      argument: false
    };
  },
  components: {
    Header,
    Field,
    Button
  },
  methods: {
    _vaildate() {
      if (isEmpty(this.phone)) {
        return "";
      }
      if (!/1[3578]\d{9}$/.test(this.phone)) {
        return "";
      }
      if (isEmpty(this.password)) {
        return "";
      }
      if (isEmpty(this.vailCode)) {
        return "";
      }
      if (!this.argument) {
        return ''
      }
    },
    submit(){
      const err = this._vaildate();
      if (err) {
        alert(err);
        return;
      }
      this.$service.register({
        phone: this.phone,
        password: this.password,
        vailCode: this.vailCode
      }).then(data=>{
        alert('!')
        this.$router.push('/login');
      })
    },
  }
};
</script>

