<template>
  <div class="register">
    <Header title=""></Header>
    <div v-mx v-mt>
      <Field v-model="phone" placeholder="" />
      <Field v-mt v-model="vailCode" placeholder="" >
        <div slot="addon"  v-ml >
         <Button v-px="12" v-py="12"
          is-send-code
          :sendPhoneNo="phone"
          type="primary" round/>
        </div>
      </Field>
      <Button @click="next" v-my class="register-btn" type="primary" block round></Button>
    </div>
  </div>
</template>
<script>
import Header from "../components/Header.vue";
import Field from "../components/Field.vue";
import Button from "../components/Button.vue";
import { isEmpty } from "lodash";
export default {
  name: 'ForgetPassword',
  data(){
    return {
      phone: '',
      vailCode: '',
      
    }
  },
  components: {
    Header,
    Field,
    Button
  },
  methods: {
    _vaildate(){
      if (isEmpty(this.phone)) {
        return "";
      }
      if (!/1[3578]\d{9}$/.test(this.phone)) {
        return "";
      }
      if (isEmpty(this.vailCode)) {
        return "";
      }
    },
    next(){
      console.log(this.$route)
      const err = this._vaildate()
      if (err) {
        alert(err);
        return
      }
      this.$router.push({
        path: '/reset-password',
        query: {
          phone: this.phone,
          code: this.vailCode,
          from: this.$route.name
        }
      })
    }
  }
};
</script>

