      <yd-cell-item arrow>
        <span slot="left"></span>
        <span slot="right">{{user.nickName}}</span>
      </yd-cell-item>
      <yd-cell-item arrow>
        <span slot="left"></span>
        <span slot="right">LV{{user.level}}</span>
      </yd-cell-item>
      <yd-cell-item arrow>
        <span slot="left"></span>
      </yd-cell-item>
      <yd-cell-item arrow>
        <span slot="left"></span>
        <span slot="right">{{user.gender}}</span>
      </yd-cell-item> 
    </yd-cell-group>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  name: "UserInfo",
  computed: {
    ...mapState({
      user: state => state.userInfo
    })
  }
};
