<template>
  <div>
    <Header title="">
      <span slot="right">
        <i class="icon icon-white-del"></i>
      </span>
    </Header>
    <yd-tab font-size="0.37333333333333335rem" :height="`${90 / 75}rem`" active-color="#369eee" v-model="selIndex" :prevent-default="false" :item-click="itemClick">
      <yd-tab-panel v-for="(item,index) in items" :key="index" :label="item.label">
        <div class="order-item" v-for="(order,index) in list" :key="index">
          <div class="products">
            <div class="order-product"  v-for="(item,j) in order.products" :key="j">
              <div class="pic">
                <img v-lazy="item.pic" alt="">
              </div>
              <div class="prod-info">
                <div class="name">{{item.name}}</div>
                <div class="color">:{{item.color}}</div>
                <div class="quantity">1</div>
                <div class="price">{{item.price}}</div>
              </div>
            </div>
          </div>
          <div class="amount">
            {{order.amount | price}} {{order.freight | price}}
          </div>
          <div class="control">
            <yd-button class="order-btn" type="danger"></yd-button>
            <yd-button class="order-btn" type="warning"></yd-button>
            <yd-button class="order-btn"></yd-button>
          </div>
        </div>
      </yd-tab-panel>
    </yd-tab>
  </div>
</template>
<script>
export default {
  name: "OrderList",
  data() {
    return {
      selIndex: 0,
      items: [
        { label: "", status: 0 },
        { label: "", status: 1 },
        { label: "", status: 2 },
        { label: "", status: 3 }
      ],
      list: [
        {
          status: 0,
          amount: 1640,
          freight: 0,
          products: [
            {
              pic: require("../assets/img/order-list/product1.jpg"),
              name: "MEINLHE-215 7",
              quantity: 1,
              color: "HE-114",
              price: 1640
            }
          ]
        },
        {
          status: 0,
          amount: 1640,
          freight: 0,
          products: [
            {
              pic: require("../assets/img/order-list/product1.jpg"),
              name: "MEINLHE-215 7",
              quantity: 1,
              color: "HE-114",
              price: 1640
            }
          ]
        }
      ]
    };
  },
  computed: {
    status() {
      return this.items[this.selIndex].status;
    }
  },
  methods: {
    itemClick(key) {
      this.$dialog.loading.open("");
      setTimeout(() => {
        this.selIndex = key;
        this.$dialog.loading.close();
        this.items[key].content =
          "key:" + key + "_" + new Date().getTime();
      }, 1000);
    }
  }
};
</script>

