<template>
  <div class="product">
    <div class="content">
      <div class="pic">
        <div class="tools">
          <div class="back ball" @click="$router.back()">
            <i class="icon icon-angle-left"></i>
          </div>
          <div class="right">
            <div class="share ball">
              <i class="icon icon-share"></i>
            </div>
            <router-link to="/cart" class="cart-link ball">
              <i class="icon icon-white-cart"></i>
              <div class="quantity">{{cartsQuantity}}</div>
            </router-link>
          </div>
        </div>
        <swiper :options="swiperOption">
          <swiper-slide v-for="(pic,index) in productDetail.pics" :key="index">
            <img :src="pic" alt="">
          </swiper-slide>
        </swiper>
        <div class="poins"></div>
      </div>
      <div class="info" v-my="10" v-px v-py="20">
        <div class="price">{{productDetail.price | price}}</div>
        <div class="raw-price">{{productDetail.rawPrice | price}}</div>
        <div class="name">{{ productDetail.name }}</div>
        <div class="other">
          <span>{{productDetail.yf}}</span>
          <span>{{productDetail.sale}}</span>
        </div>
      </div>
      <yd-cell-group>
        <yd-cell-item arrow>
          <span slot="left">
            7    
          </span>
        </yd-cell-item>
        <yd-cell-item arrow>
          <span slot="left">
             ...
          </span>
        </yd-cell-item>
      </yd-cell-group>
      <yd-cell-group>
        <yd-cell-item arrow>
          <span slot="left">
            
          </span>
        </yd-cell-item>
        <yd-cell-item arrow>
          <span slot="left" @click="showChooseColor">
            
          </span>
        </yd-cell-item>
      </yd-cell-group>

      <yd-popup v-model="show" position="bottom">
        <yd-cell-group title="" v-p>
          <yd-cell-item type="radio" v-for="(item,index) in productDetail.colors" :key="index">
            <span slot="left">{{item}}</span>
            <input slot="right" type="radio" :value="item" v-model="color" />
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left"></span>
            <span slot="right">{{color}}</span>
          </yd-cell-item>
        </yd-cell-group>
      </yd-popup>

      <div v-p v-pt="30" v-my="10" class="comment-wrap">
        <h2>({{ productDetail.comments.length }})</h2>
        <div class="comment" v-for="(comment,index) in productDetail.comments" :key="index">
          <yd-flexbox class="userInfo">
            <div class="avatar" v-mr="20">
              <img v-lazy="comment.userInfo.avatar" alt="">
            </div>
            <yd-flexbox-item class="username">{{comment.userInfo.username}}</yd-flexbox-item>
          </yd-flexbox>
          <p class="content">{{comment.content}}</p>
          <div class="color">{{comment.product.color}}</div>
        </div>
        <yd-flexbox class="more">
          <Button outline type="primary" v-px="34" round v-py="20"></Button>
        </yd-flexbox>
      </div>
      <div class="detail-pics">
        <h2 v-my></h2>
        <img v-lazy="pic" :key="index" v-for="(pic,index) in productDetail.detailPics" alt="">
      </div>
    </div>
    <yd-flexbox class="bottom" align="center">
      <yd-flexbox-item>
        <yd-flexbox>
          <yd-flexbox-item>
            <div class="item">
              <i class="icon icon-kefu"></i>
              <p></p>
            </div>
          </yd-flexbox-item>
          <yd-flexbox-item>
            <div class="item">
              <i class="icon icon-star"></i>
              <p></p>
            </div>
          </yd-flexbox-item>
        </yd-flexbox>
      </yd-flexbox-item>
      <yd-flexbox-item>
        <Button type="primary" @click="addCart" outline round></Button>
        <Button type="primary" round></Button>
      </yd-flexbox-item>
    </yd-flexbox>
  </div>
</template>
<script>
import { mapActions, mapState, mapGetters } from "vuex";
export default {
  name: "Product",
  computed: {
    ...mapGetters(["cartsQuantity"])
  },
  data() {
    return {
      swiperOption: {
        pagination: {
          el: ".poins",
          bulletClass: "point",
          bulletActiveClass: "point-active"
        }
      },
      productDetail: {
        pics: [
          require("../assets/img/products/detail/main.jpg"),
          require("../assets/img/products/detail/main.jpg"),
          require("../assets/img/products/detail/main.jpg"),
          require("../assets/img/products/detail/main.jpg"),
          require("../assets/img/products/detail/main.jpg")
        ],
        name: "GP 148 ",
        yf: 10,
        sale: 159,
        price: 36800,
        rawPrice: 46600,
        colors: ["", "", ""],
        comments: [
          {
            userInfo: {
              id: 1,
              avatar: require("../assets/img/products/detail/avatar.jpg"),
              username: ""
            },
            content: ``,
            product: {
              color: ""
            }
          }
        ],
        detailPics: [
          require("../assets/img/products/detail/detail1.jpg"),
          require("../assets/img/products/detail/detail2.jpg")
        ]
      },
      color: "",
      product_id: this.$route.params.id,
      show: false
    };
  },

  methods: {
    ...mapActions(["addProductToCarts"]),
    showChooseColor() {
      this.show = true;
    },
    addCart() {
      /* 
        idchecked
      */
      const result = this.addProductToCarts({
        id: this.product_id,
        pic: this.productDetail.pics[0],
        checked: false,
        quantity: 1,
        price: 399,
        name: this.productDetail.name
      });

      if (result) {
        this.$dialog.toast({
          mes: "",
          timeout: 1500,
          icon: 'success'
        });
      }
    }
  }
};
</script>

